import {Container,Grid,Typography,TextField,Button} from '@mui/material'
import { signin } from './service/APIService'
import { Link } from 'react-router-dom';
import { NagivationBar } from './Navigater';



export const Login = () => {

    //form
    const handleSubmit = (e) => {
        e.preventDefault(); //    
        //React SPA      ,   
        //    .

        //submit(, )   .
        const data = new FormData(e.target); //submit form .
        const username = data.get("username");  // username  .
        const password = data.get("password"); //password  

        // //, ()
        console.log("",username)
        console.log("",password)

        //signin  
        //APIService signin    ( userDTO)
        signin({username : username, password : password})
    }



    return (
        
<>
        <NagivationBar></NagivationBar>
        <Container component="main" maxWidth="xs" style={{marginTop:"8%"}}>

            

            

            <Typography component="h1" variant='h3' align='center' gutterBottom>
                        
            </Typography>

  

            <form noValidate onSubmit={handleSubmit}>
                <Grid container spacing={2}  direction="column">
                    <Grid item xs ={12}>
                        <TextField
                            variant='outlined'
                            required
                            fullWidth
                            id="username"
                            label=" "
                            type='email'
                            name='username'
                            autoComplete='username'
                        />
                    </Grid>
                    <Grid item xs ={12}>
                        <TextField
                            variant='outlined'
                            required
                            fullWidth
                            id="password"
                            label=""
                            name='password'
                            type='password'
                            autoComplete='password'
                        />

                    </Grid>
                    <Grid item xs={12}>
                        <Button 
                        type='submit'
                        fullWidth
                        variant='contained'
                        color='primary'>
                            
                        </Button>
                    </Grid>
                </Grid>
                <Grid item direction='column'>
                    <Link to="/signup" >
                     ?  
                    </Link>
                </Grid>
            </form>
        </Container>
</>
    )
}