//  checkBox label  
import { useState } from "react";
import {ListItem, 
    ListItemText, 
    InputBase, 
    Checkbox, 
    listItemSecondaryAction, 
    IconButton,
    ListItemSecondaryAction} from '@mui/material'
import DeleteOutlined from '@mui/icons-material/DeleteOutlineOutlined'

    //ListItemSecondaryAction
    //ListItem          .
    // ListItem  .

    //IconButton
    //     .

    //DeleteOutlined
    //Mui      .
   

let Todo = (props) => {

    const editiItem = props.editiItem

    //App.js    
    const[item,setItem] = useState(props.item);
    const [readOnly,setReadOnly] = useState(true);

    //true -> false  trunOffReadOnly 
    const trunOffReadOnly = () => {
        setReadOnly(false);
        console.log("Active!")
        console.log(readOnly)
    }

  


    //(2)
    const deleteItem = props.deleteItem;
    const deleteEventHandler = () => {
        deleteItem(item);
      }

    // //(3)
    // //  
    // const handleChange = (e) =>{
    //     setItem({
    //         ...item,
    //         title:e.target.value
    //     })
    // }
    const turnOnReadOnly = (e) =>{
        if(e.key=='Enter' && readOnly===false){
            setReadOnly(true); //readOnly true  .
            editiItem(item);
        }
    }

      //  
    const checkBoxEventHandler = (e) => {
        item.done = e.target.checked
        editiItem(item);
    }

   
    // (onChange)
    const editEventHandler = (e) =>{
        setItem({...item,title:e.target.value})
    }


    return(
        //Html  
        //    
        //onclick -> onClick
        //class -> className
        // <div className = "Todo">
        <ListItem>
            <Checkbox  onClick={checkBoxEventHandler} />
            <ListItemText>
                <InputBase
                    inputProps={{"aria-label" : "naked", "readOnly": readOnly}}
                    onClick={trunOffReadOnly}
                    onChange={editEventHandler}
                    onKeyDown={turnOnReadOnly}

                    type="text"
                    id={item.id}
                    name={item.id}
                    value={item.title}
                    multiline={true}
                    fullWidth={true}
                />
            </ListItemText>

            <ListItemSecondaryAction>
                <IconButton onClick={deleteEventHandler} aria-label="Delete Todo">
                    <DeleteOutlined />
                </IconButton>
            </ListItemSecondaryAction>

        </ListItem>
    )
}


export default Todo;