import { useState } from "react";
import {Button, Grid,TextField} from '@mui/material'

    //Container
    //  ,        
    
    // props
    //maxWidth :   (xs, sm, md, lg, xl, false)


    //Button
    // (variant) : text, contatined,outlined
    // (color), (size)   


    //Grid2
    //CSS Grid    
    //Flexbox   Grid ,  , rowSpacing, columnspacing
    //   .
    // props
    //container :   
    //item :   (  )
    //xs(0), sm(600), md(900), lg(1200), xl(1536) :    ( 12)
    // :   
    //columns :   ( 12)
    //rowSpacing, columnSpacing : , 

    //TextField
    //(input), (label),  (helperText), (error) 
    //   
    // props
    //variant :  
    //label :  
    //helperText :   
    //error :  true  
    //fullWidth :  100% 
    //multiline :   
    //rows : multiline    
    //type : text, password, email   

    //react style   js  .
   
    //App.js  add'' .
    //props      .
    const AddTodo = ({add}) =>{

    const[item,setItem] = useState({title:" "});

    // e.target  value   setItem .
    const onInPutChange = (e) =>{
        setItem({title:e.target.value});
        console.log({title:e.target.value});
    }
    
    //'+'     
    //App.js  add(item)  .
    const onButtonClick = () =>{
        if(item.title.trim() ===''){
            alert(' ')
            return; //   .
        }

        //props  add 
            //    App.js .
        add(item); //add({title:''})
        setItem({title:""}) //      .
    }

    //     
    const enterKeyEventHandler = (e) => {
        if(e.key =="Enter"){
            onButtonClick();
        }
    }

   

    return(
        //contianer
    <Grid container style={{marginTop:20}} >
        <Grid xs={11} md={11} item style={{paddingRight:8}}>
            <TextField color="secondary"  placeholder="Add Todo here" fullWidth
            value={item.title} onChange={onInPutChange}
            onKeyDown={enterKeyEventHandler}
            />
        </Grid>
        <Grid>
            <Button onClick={onButtonClick} fullWidth style={{height:'100%', marginRight:20}} color="secondary" variant="outlined">+</Button>
        </Grid>

    </Grid>
    )
}

export default AddTodo;